import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

async function fetch({FarmId, PenId}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/pens";
    if (PenId){
      ref += "/" + PenId
    }
    var res = await database().ref(ref).once('value').then((_pensData) => {
      if (PenId) {
        var penData = _pensData.val()
        if (!penData) penData = {}
        if (!penData.datas) penData.datas = {}

        var res = {[PenId]: penData}
        var state = _rend.getState()
        if (state.firebase.pens){
          res = {...state.firebase.pens}
          res[PenId] = penData
        }
        return res;
      } else {
        var res = _pensData.val()
        return res
      }
    })
    return res
  }
  return handleError({ message: 'Fecth Pens Error' })
}

async function update({FarmId, PenId, Pen}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    if (PenId) {
      var res = await fetch({FarmId: FarmId, PenId: PenId}).then(async (_Pen) => {
        var ref2 = ref + "pens/" + PenId + "/"
        var data = Pen.datas
        var _data = _Pen[PenId].datas
        var Datas = {..._data, ...data}
    
        _Pen[PenId].datas = Datas
        var res2 = await database().ref(ref2).update({
          ..._Pen[PenId],
        }).then(async () => {
          var penData = _Pen[PenId]
          return {[PenId]: penData}
        }).catch(error => {
          console.log("Update Pen Error: ", error);
          return handleError({ message: 'Update Pens Error' })
        });
        return res2
      })
      return res
    }
  }
} 

export {
  fetch,
  update,
}